.todoitem{
    border-radius: 10px;
    width: 100%;
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 0 8px rgb(180, 245, 3);
    align-items: center;
    background-color: rgb(101, 160, 152);
    padding: 15px;
}
.todoitem:hover{
    transform: scale(1.1);
box-shadow: 0 0 15px 7px rgba(255, 255, 255, 0.2);
transition: box-shadow 0.5s, transform 0.5s;
}

.todoitem__check{
    box-shadow: 0 0 5px rgb(180, 245, 3);
    margin-right: 30px;
}
.todoitem__titel{
    text-shadow: #FC0 1px 0 10px;
    margin-right: auto;
}
.todoitem__btn{
    padding: 5px;
    border-radius: 5px;
    background-color: red;
    box-shadow: 0 0 5px rgb(180, 245, 3);
}
