.todolist{
    border-radius: 10px;
    display: flex;
    margin: 20px auto;
    padding: 40px;
    flex-direction: column;
   width: 45%;
   align-items: flex-start;
   background-image: url("../../Assets/img/newimg.webp");
   background-repeat: no-repeat;
   background-size:100% 100% ;
   box-shadow: 10px 10px 10px 2px rgba(34, 60, 80, 0.2);
}
.todoTitle{
    background: -webkit-linear-gradient(rgb(99, 177, 88), rgb(119, 44, 44));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.todoinput{
    width: 400px;
    height: 40px;
    /* border: rgb(253, 253, 253); */
    border-color: rgb(47, 37, 139);
    border-radius: 10px;
    color: wheat;
    padding: 10px;
    background-color: transparent;
    font-size: 20px;
}
.todoinput:hover{
    transform: scale(1.1);
    box-shadow: 0 0 15px 7px rgba(240, 242, 243, 0.2);
    transition: box-shadow 0.5s, transform 0.5s;
    background-color: rgba(244, 245, 244, 0.479);
}
.todobtn{
width: 60px;
height: 40px;
border-radius: 10px;
margin: 20px;
}
.todobtn:hover{
    transform: scale(1.2);
    box-shadow: 0 0 15px 7px rgba(250, 253, 255, 0.2);
    transition: box-shadow 0.5s, transform 0.5s;
}